import cTitle from 'components/title';
export default {
    data() {
        return {
            selected: '1',
            display: 1,
            pagesize:1,
            //待使用
            wait_used:[],
            //已过期
            overdue:[],
            used:[]

        }
    },
    methods: {
        toStore() {
            this.$router.push({ name: "couponStore", params: { myparent: this } });
        },
        goHome()
        {
            this.$router.push('/home');

        },
        //切换优惠券状态列表
        switchItem() {
            this.getNetData(this.selected);
        },
        toggle(event, n) {
            console.log(event)
            if (event.target.className == 'fa coupon_voucher_more_arr fa-angle-down') {
                this.display = 99999;
            } else {
                this.display = n;
            }
        },
       
        //发起http请求
		getNetData(type) {
		
			var that = this;
			$http.get('coupon.member-coupon.coupons-of-member-by-status', { status_request:type }, '正在获取...').then(function (response) {

				if (response.result == 1) {
					console.log("#################");
					console.log(response.data);
					var myData = response.data;
					if(type == 1)
                    {
                        that.wait_used = myData;

                    }else if(type == 2)
                    {
                        that.overdue = myData;

                    }else
                    {
                        that.used = myData;

                    }

				}
			}, function (response) {
				// error callback
			});

		},

    },
    activated() {
        this.getNetData(1);
        this.$on('selected_coupon', (params) => {
            console.log(params.coupon_id);
        });
    },
    components: { cTitle }
}




